<template>
  <div>
    <div>监控</div>
    <div class="someClass" :style="style">动态文字</div>
    <input type="text" />
  </div>
</template>

<script>
export default {
  name: "Monitor",
  data() {
    return {
      style: {
        color: "red", //样式
        fontSize: "50px", //样式
        backgroundColor: "Indigo", //多个单词使用驼峰模式
        opacity: 1, //样式
      },

    };
  },
  mounted() {
    console.log("监控组件加载完成", this);
  },
  beforeDestroy() {
    console.log("监控组件销毁完成");
  },
  //路由组件的生命周期激活时，
  activated() {
    console.log("Monitor组件激活了...");
    // 定时器（在vm中存放了一个定时器对象 ）
    this.timer = setInterval(() => {
      this.style.opacity -= 0.01;
      // console.log(this.data_style.opacity);
      if (this.style.opacity < 0) {
        this.style.opacity = 1;
      }
      console.log(this.style.opacity);
    }, 10);
  },
  //路由组件的生命周期失活时
  deactivated() {
    console.log("Monitor组件失活了...");
    clearInterval(this.timer);
  }, 
  //组件通过路由的过滤器，进入该组件时被调用（to去哪里，from来自哪里，next放行）
  beforeRouteEnter(to, from, next) {
    console.log("通过路由规则，进入该组件时被调用"); //获取路由的参数
   //获取cookies判断用户是否存在
      if (window.localStorage.getItem("token") === "admin") {
        //放行
        next();
      } else {
       next("/login");//跳转到登录页面
      }
    
  }, //组件通过路由的过滤器，，离开该组件时被调用 （to去哪里，from来自哪里，next放行）
  beforeRouteLeave(to, from, next) {
    console.log("通过路由规则，离开该组件时被调用"); //放行
    next();
  },
};
</script>

<style scoped>
/* 定义动画 */
@keyframes changeColor {
  0% {
    color: red;
  }
  50% {
    color: blue;
  }
  100% {
    color: green;
  }
}

/* 将动画应用到元素上 */
</style>